import React from 'react'
import './inputbox.css'

export default function InputBox({nextPage}) {
    const [word, setWord] = React.useState('')
    return (<div style={{display: 'flex', gap: '12px', flexWrap: 'wrap', alignContent: 'center', justifyContent: 'center'}}>
        <input className="inputBox" value={word} onChange={e => setWord(e.target.value)} placeholder=" "/>
        <button onClick={() => nextPage({searchKeyword: word})} className="inputSubmit"></button>
    </div>)
}
