import React from 'react'
import './App.css'

import Home from './page/home'
import Segment from './page/segment'
import Classify from './page/classify'
import Topic from './page/topic'
import Search from './page/search'
import Contact from './page/contact'
import axios from 'axios'

const url = 'https://backend.ourdataflow.com'

function App() {
  const [id, setId] = React.useState('')
  const [page, setPage] = React.useState(0)
  const [status, setStatus] = React.useState({})
  const nextPage = (update) => {
    setPage(page + 1)
    const unify = {
      ...status,
      ...update,
    }
    console.log(unify)
    setStatus(unify)
    axios.post(`${url}/leads`, JSON.stringify({
      id: new Date().now,
      ...status,
    }), {
      headers: {'Content-Type': 'application/json'},
    })
  }
  
  React.useEffect(() => {
    var res = localStorage.getItem('key')
    if (!res) {
      res = Math.floor(Math.random() * 100000)
      localStorage.setItem('key', res)
    }
    setId(res)
  }, [])

  return (
    <div className="App" style={{width: '100vw', height: '100vh', overflowX: 'hidden', overflowY: 'scroll'}}>
      {page === 0 ? <Home nextPage={nextPage} />: ''}
      {page === 1 ? <Classify nextPage={nextPage} />: ''}
      {page === 2 ? <Segment nextPage={nextPage} />: ''}
      {page === 3 ? <Topic stat={status} nextPage={nextPage} />: ''}
      {page === 4 ? <Search stat={status} nextPage={nextPage} />: ''}
      {page === 5 ? <Contact stat={status} />: ''}
    </div>
  );
}

export default App;

const backStyle = {
  transform: "scale(-1, 1)",
  position: 'absolute',
  left: "30px",
  top: "30px",
  width: "32px",
  height: '32px',
}
