import React from 'react'
import './segment.css'

import TopicCard from '../components/topiccard'

const cards = [
    {title: 'Human Language', desc: 'Ex)   ,  '},
    {title: 'Health Care', desc: 'Ex)   ,  '},
    {title: 'Automobile', desc: 'Ex)     ,   '},
    {title: 'Retail', desc: 'Ex)    ,  '},
    {title: 'Financial', desc: 'Ex)   ,    '},
    {title: 'Manfacture', desc: 'Ex)    ,   '},
    {title: 'Media/Entertainment', desc: 'Ex)    ,   '},
    {title: 'Agriculture', desc: 'Ex)    ,    '},
    {title: 'Environment/Safety', desc: 'Ex)     ,   '},
]

export default function Segment({nextPage}) {
    return <div className="segmentPage">
        {cards.map((card, idx) => <TopicCard title={card.title} desc={card.desc} nextPage={() => nextPage({fieldId: idx+1})} />)}
    </div>
}
