import React from 'react'
import './topic.css'
import axios from 'axios'

import TopicCard from '../components/topiccard'

const url = 'https://backend.ourdataflow.com'

export default function TopicPage({stat, nextPage}) {
    const [categories, setCate] = React.useState([])

    React.useEffect(() => {
        axios.get(`${url}/datasets/categories?fieldId=${stat.fieldId}`)
            .then(res => setCate(res.data))
    }, [])

    return (<div className="topicPage">
        <div className="title">Which data<br/>are you searching?</div>
        {categories.map(card => <TopicCard title={card.koName} desc={card.enName} nextPage={() => nextPage({categoryId: card.id})} />)}
    </div>)
}
