import React from 'react'
import './contact.css'
import {Slider} from '@material-ui/core'
import axios from 'axios';

const customMarks = [
    {
      value: 50,
      label: '1',
    },
    {
      value: 40,
      label: '1000',
    },
    {
      value: 30,
      label: '100',
    },
    {
      value: 20,
      label: '10',
    },
    {
      value: 10,
      label: '0',
    },
  ];

const url = 'https://backend.ourdataflow.com'

const run = (status, value, name, dept, role, email) => {
  axios.post(`${url}/leads`, JSON.stringify({
    id: "1",
    ...status,
    priceBar: value,
    name,
    company: dept,
    position: role,
    email,
  }), {
    headers: {'Content-Type': 'application/json'},
  })
}

export default function Contact({stat}) {
    const [value, changeValue] = React.useState(10)
    const [name, setName] = React.useState('')
    const [dept, setDept] = React.useState('')
    const [role, setRole] = React.useState('')
    const [email, setEmail] = React.useState('')
    return <div className="contactPage">
        <div className="about">
            <div className="title">Contact</div>
            <div className="desc">    <br/> . <br/>  .</div>
        </div>
        <div className="form">
        <div className="title">What's your price?</div>
            <Slider
                style={{marginBottom: "50px", minWidth: '250px'}}
                min={10}
                max={50}
                step={10}
                value={value}
                marks={customMarks}
                onChange={(e, v) => {
                    changeValue(v)
                }}          
            />
            <input value={name} onChange={(e) => setName(e.target.value)} className="input" placeholder={""}/>
            <input value={dept} onChange={(e) => setDept(e.target.value)} className="input" placeholder={""}/>
            <input value={role} onChange={(e) => setRole(e.target.value)} className="input" placeholder={""}/>
            <input value={email} onChange={(e) => setEmail(e.target.value)} className="input" placeholder={"Email"}/>
            <button className="button" onClick={() => run(stat, value, name, dept, role, email)}></button>
        </div>
    </div>
}
