import React from 'react'
import './search.css'
import axios from 'axios'

import TopicCard from '../components/topiccard'
import InputBox from '../components/input'

const url = 'https://backend.ourdataflow.com'

export default function Search({stat, nextPage}) {
    const [keyword, setKeyword] = React.useState({})
    const [dt, setDT] = React.useState([])

    React.useEffect(() => {
        console.log(keyword)
    }, [keyword])

    React.useEffect(() => {
        axios.get(`${url}/datasets?fieldId=${stat.fieldId}&categoryId=1`)
            .then(res => setDT(res.data))
    }, [])

    return <div className="searchPage">
        <div className="header" style={{display: 'flex', flexWrap: 'wrap'}}>
            <div className="title">DataSet</div>
            <div style={{width: '550px', margin: '0px 0px 10px 0px'}}><InputBox nextPage={(val) => setKeyword(val)} /></div>
        </div>
        <div style={{margin: '10px 0px', width: '0px'}}></div>
        {dt.map(dt => <TopicCard title={dt.name} desc={dt.fileSize} nextPage={() => nextPage({
            datasetSearchKeyword: keyword ? keyword.searchKeyword: '',
            datasetId: dt.id,
        })} />)}
    </div>
}

